import React from 'react';
import SVG from 'react-inlinesvg';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { selectOptns } from '../../../../../Services/Utils/config';
const MoveTo = React.lazy(() => import('../Actions/MoveTo'));
const BulkCopy = React.lazy(() => import('../Actions/BulkCopy'));

const SelectAllOptions = ({ myAssetsData, addTagModalShow, collections, postPostMove, onSelectToMove, onSelectToCopy, onSelectMoveToTrash, rightsHandleModalShow, onSelectPostStatus }) => {
    const options = selectOptns(addTagModalShow, rightsHandleModalShow, onSelectMoveToTrash, onSelectPostStatus)
    return <>
        <div className={`${Object.keys(myAssetsData.selectedData).length > 0 ? `d-flex` : `d-none`} align-items-center flex-wrap my-3`}>
            <div className="d-none d-lg-flex">
                {options.map((item,index)=> {
                    return <>
                        {index == 0 ? <MoveTo collections={collections} onSelectToMove={onSelectToMove} /> : null}
                        {index == 3 ? <BulkCopy collections={collections} postPostMove={postPostMove} onSelectToCopy={onSelectToCopy} /> : null}
                        <button type="button" className="btn p-0 d-inline-flex align-items-center me-2" onClick={item.fnc}>
                            {item.name != "Get Rights" ? <i className={`${item.icon} icon-6 me-1`}></i> :
                            <span className="svg-icon svg-icon-5 text-gray-800">
                                <SVG src={baseUrl(`media/icons/svg-icons/${item.icon}.svg`)} height={16} width={16} />
                            </span>}
                            <span className="text-gray-800 fs-7">{item.name}</span>
                        </button>
                    </>
                })}
            </div>
        </div>
    </>
}
export default SelectAllOptions